import React, { useState } from "react";
import "./ItemsList.css";
import { ReactComponent as Create } from "../assets/create.svg";
import EditQuantitiesMenu from "./EditQuantitiesMenu";

const ItemsList = ({ itemlist }) => {
  const [quantitiesMode, setQuantitiesMode] = useState({});

  return (
    <div className="shopping-list-container">
      <div className="shopping-list-title">
        <h1>Shopping List</h1>
        <Create />
      </div>

      {itemlist.map((i) => {
        return (
          <div className="shopping-items-container">
            <h1 className="shopping-list-category">{i.category}</h1>
            {i.items.map((i) => (
              <div className="shopping-item-container">
                <p className="shopping-item">{i.name}</p>
                {quantitiesMode[i.name] ? (
                  <EditQuantitiesMenu>
                    <button
                      onClick={() =>
                        setQuantitiesMode({
                          ...quantitiesMode,
                          [i.name]: false,
                        })
                      }
                      className="shopping-quantity-button"
                    >
                      {i.quantity} pcs
                    </button>
                  </EditQuantitiesMenu>
                ) : (
                  <button
                    onClick={() =>
                      setQuantitiesMode({ ...quantitiesMode, [i.name]: true })
                    }
                    className="shopping-quantity-button"
                  >
                    {i.quantity} pcs
                  </button>
                )}
              </div>
            ))}
          </div>
        );
      })}
    </div>
  );
};
export default ItemsList;
